import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import Layout from '@/layout/Layout.vue'
import NoNavigationLayout from '@/layout/NoNavigationLayout.vue'
import FavoriteLayout from '@/layout/FavoriteLayout.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    // @ts-ignore
    component: Layout,
    children: [
      //  ホーム
      {
        path: '',
        name: 'Home',
        component: () => import('../views/Home.vue')
      },
      //  検索
      {
        path: '/search',
        name: 'Search',
        component: () => import('../views/Search.vue')
      },
      //  一覧/検索結果
      {
        path: '/result-list',
        name: 'ResultList',
        component: () => import('../views/ResultList.vue')
      },
      //  コース詳細
      {
        path: '/course-detail/:id',
        name: 'CourseDetail',
        component: () => import('../views/CourseDetail.vue')
      },
      //  講師の詳細
      {
        path: '/teacher-detail/:id',
        name: 'TeacherDetail',
        component: () => import('../views/TeacherDetail.vue')
      },
      //  マイページ
      {
        path: '/mypage',
        name: 'Mypage',
        component: () => import('../views/Mypage.vue')
      },
      //  言語選択
      {
        path: '/change-language',
        name: 'ChangeLanguage',
        component: () => import('../views/ChangeLanguage.vue')
      },
      //  デバイス選択
      {
        path: '/device-link',
        name: 'DeviceLink',
        component: () => import('../views/DeviceLink.vue')
      },

      //  受講コース
      {
        path: '/take-course-history',
        name: 'TakeCourseHistory',
        component: () => import('../views/TakeCourseHistory.vue')
      },
      //  通知
      {
        path: '/notice',
        name: 'Notice',
        component: () => import('../views/Notice.vue')
      },
      //  通知詳細
      {
        path: '/notice-detail',
        name: 'NoticeDetail',
        component: () => import('../views/NoticeDetail.vue')
      },
      //  受講しているコース
      {
        path: '/take-course-detail',
        name: 'TakeCourseDetail',
        component: () => import('../views/TakeCourseDetail.vue')
      },
      //  技能認定リスト
      {
        path: '/admitted-skill-list',
        name: 'AdmittedSkillList',
        component: () => import('../views/AdmittedSkillList.vue')
      },
      //  講師に質問する
      {
        path: '/question-form',
        name: 'QuestionForm',
        component: () => import('../views/QuestionForm.vue')
      },
      //  レビュー投稿
      {
        path: '/review-form',
        name: 'ReviewForm',
        component: () => import('../views/ReviewForm.vue')
      }
    ]
  },
  {
    path: '/favorite',
    // @ts-ignore
    component: FavoriteLayout,
    children: [
      //  お気に入り（コース）
      {
        path: '',
        name: 'FavoriteCourse',
        component: () => import('../views/FavoriteCourse.vue')
      },
      //  お気に入り（講師）
      {
        path: 'teacher',
        name: 'FavoriteTeacher',
        component: () => import('../views/FavoriteTeacher.vue')
      }
    ]
  },
  {
    //　ボトムナビゲーションなし
    //  通知（メッセージ）
    path: '/notice-message/:id',
    component: NoNavigationLayout,
    children: [
      {
        path: '',
        name: 'NoticeMessage',
        component: () => import('../views/NoticeMessage.vue')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(savedPosition)
        })
      })
    } else {
      return { left: 0, top: 0 }
    }
  }
})

export default router
