
import { computed, defineComponent } from 'vue'
import { faHome, faList, faSearch, faUserCircle } from '@fortawesome/free-solid-svg-icons'
import { faBell } from '@fortawesome/free-regular-svg-icons'

export default defineComponent({
  setup() {
    const homeIcon = computed(() => faHome)
    const searchIcon = computed(() => faSearch)
    const listIcon = computed(() => faList)
    const bellIcon = computed(() => faBell)
    const userIcon = computed(() => faUserCircle)

    return {
      homeIcon,
      searchIcon,
      listIcon,
      bellIcon,
      userIcon
    }
  }
})
