import { createStore } from 'vuex'
import { useQuery, useResult } from '@vue/apollo-composable'
import { StudentsQuery } from '@/graphql/@types/types'
import studentQuery from '@/graphql/queries/sutudent.query'
// @ts-ignore
import callAndroidThreadMaker from '@/callAndroidMethod/threadMaker'
export interface BluetoothDevice {
  deviceName: null | string
  macAddress: null | string
}

interface IState {
  isLoading: boolean
  currentUser: undefined | StudentsQuery
  connectingBluetoothDevice: BluetoothDevice
}

const state: IState = {
  isLoading: false,
  currentUser: undefined,
  connectingBluetoothDevice: {
    deviceName: null,
    macAddress: null
  }
}

export default createStore({
  state,
  mutations: {
    setLoading(state, payload) {
      state.isLoading = payload
    },
    setCurrentUser(state, payload) {
      state.currentUser = payload
    },
    setConnectingBluetoothDevice(state, payload: BluetoothDevice) {
      state.connectingBluetoothDevice.deviceName = payload.deviceName
      state.connectingBluetoothDevice.macAddress = payload.macAddress
    },
  },
  actions: {
    async requestGetStudent({ commit }) {
      const { result } = await useQuery<StudentsQuery>(studentQuery)
      const user = useResult(result, null, (data) => data.Students[0])
      commit('setCurrentUser', user)
    },
    async submitBTDeviceData(context, payload) {
      const result = await callAndroidThreadMaker(state.connectingBluetoothDevice.macAddress,payload.movieData);
      return result.result
    },
    async requestSetConnectingBluetoothDevice({commit}, payload) {
      await commit('setConnectingBluetoothDevice', payload.device)
    }
  },
  modules: {}
})
